<template>

<div  class="game pa" unselectable="on" onselectstart="return false;" onmousedown="return false;">

<div class="bars">
  <div class="bar-vie-block" >
  <img  :src="'/img/heart'+ vieInOff + '.svg'" class="coeur-vie"/> 
  <div  :class="'bar-vie'+ vieInOff" >
  <div :style="{width: vie + '%'}"></div>
  </div>
</div>
<div class="bar-time-block" style="margin-left:5%;">
  <img  :src="'/img/hourglass'+ timeInOff + '.svg'" class="sablier-time"/>
  <div  :class="'bar-time'+ timeInOff" >
   <div :style="{width: temps + '%'}"></div>
  </div>
</div>
</div>

  <cartes v-for="car in carte"  :key="car" :nom="car" :size_carte="nbrCarte" :carte="carteActive" v-on:essai_carte=tryCarte ></cartes>


    

  </div>
</template>


<script>
// @ is an alias to /src
import Cartes from '@/components/Cartes.vue'

export default {
  name: 'Game',
  components: {
    Cartes
  },
  computed:{
    
     niveau(){
      
     return this.$store.state.niveau;
    },
    nbrCarte(){
      
     return this.$store.state.nbrCarte;
    },

   carteActive(){
      
     return this.$store.state.carteActive;
    },

     carte(){
      
     return this.$store.state.carte;
    },
 

     vieInOff(){
      
     return this.$store.state.vieInOff;
    },
   

     timeInOff(){
      
     return this.$store.state.timeInOff;
    }, essai1(){
      
     return this.$store.state.essai1;
    }, vie(){
      
     return this.$store.state.vie;
    }, temps(){
      
     return this.$store.state.temps;
    }, time(){
      
     return this.$store.state.time;
    }, stopTimer(){
      
     return this.$store.state.stopTimer;
    }, win(){
      
     return this.$store.state.win;
    }, bad(){
      
     return this.$store.state.bad;
    },onGame(){
      
     return this.$store.state.onGame;
    },
    
  },

methods:{  

        
        createCarte:function(num){
          
          this.$store.commit('addCartes', num);
          this.$store.commit('shuffleCartes', this.carte);
          this.$store.commit('setOnGame', true);
          var n =this.niveau+"";
          
          n=n.substring(n.length-1);
          n=parseInt(n, 10);
          switch (n) {
          case 3: case 4:  case 7:  case 8:  case 9:  case 0:
          this.$store.commit('setVieInOff',""); 
          break;
          default:
          this.$store.commit('setVieInOff',"-off");
          }
          switch (n) {
          case 5: case 6:  case 7:  case 8:  case 9:  case 0:
          this.$store.commit('setTimeInOff',""); 
          var timestamp=Date.now();
          timestamp=Math.floor(timestamp/1000);
          this.$store.commit('setTime',timestamp);
          this.minuteur();
          break;
          default:
          this.$store.commit('setTimeInOff',"-off");
          }
          
 
      
        },
        tryCarte:function(num){

          var e1=this.essai1;

      if(!e1){
        this.noneCarte(num);
        this.$store.commit('setEssai1', num);
        
      
      }else if (e1.substring(1, 2)!=num.substring(1, 2))
      {
        this.noneCarte();
        
        setTimeout( function () { 
        if(!this.vieInOff){ 
          this.$store.commit('setBad', this.bad+1);

          var n =this.niveau+"";
          n=n.substring(n.length-1);
          n=parseInt(n, 10);
          switch (n) {
          case 0: case 9:
          this.$store.commit('setVie', this.vie-20);
          break;
          default:
          this.$store.commit('setVie', this.vie-10);
          }
          
          
        
        if(this.vie<=0){
           this.clearAllInterval();
          this.$store.commit('resetGame', false); 
          this.$router.push('/');
        }else{
        this.clearCarte(num);  
        this.autoCarte();   
         
        }

        }else{
        this.clearCarte(num);  
        this.autoCarte();  
         
          
        
        
        }
        }.bind(this) , 400);
      }else{
         this.noneCarte();
        
          var n =this.niveau+"";
          n=n.substring(n.length-1);
          n=parseInt(n, 10);
          switch (n) {
           case 1:  case 3: case 5:  case 7:  
          
          
         setTimeout( function () {  
         this.laisserCarte(num);
         this.autoCarte(); 
         this.$store.commit('setWin', this.win+1);
         if(this.win==this.nbrCarte){this.$store.commit('resetGame', true);
         
          this.clearAllInterval();
         this.$router.push('/');}}.bind(this) , 400);

          
          break;
          default:
           setTimeout( function () { 
          this.removeCarte(num);
          this.autoCarte(); 
          this.$store.commit('setWin', this.win+1);
          if(this.carte.length<=0){
                this.clearAllInterval();
            this.$store.commit('resetGame', true);
            this.$router.push('/');
            }
          
          }.bind(this) , 400);
          }


        
   }
      
        },

        
    autoCarte: function(id){
    
      if(!id){
      for(var i=0;i<this.carte.length;i++){
        let im=this.carte[i];
        let img=document.getElementById(im);

        let srcCarte=img.src.substring(img.src.length-14);
        srcCarte=srcCarte.substring(8,0);
       
         if(srcCarte=="img/card"){ img.style="pointer-events:auto"; }
      } }
      else {
        let img=document.getElementById(id);
        let srcCarte=img.src.substring(img.src.length-14);
        srcCarte=srcCarte.substring(8,0);
       
        if(srcCarte=="img/card"){ img.style="pointer-events:auto"; }
      }

   
    },
        laisserCarte: function(num){
    
  
       
        var cart = document.getElementById(num);
     
      var essai= document.getElementById(this.essai1);
      cart.style="pointer-events:none";
      essai.style="pointer-events:none";
      
      


      this.$store.commit('setEssai1', "");
  
  
 
    },

    
    removeCarte(numCarte){
    
      let cart1= document.getElementById(numCarte);
      let e1 = document.getElementById(this.essai1);
      cart1.src="img/card-"+this.carteActive+".png";
      e1.src="img/card-"+this.carteActive+".png";
       
      for( let i = 0; i < this.carte.length; i++){ 
  

        if ( this.carte[i] === numCarte) { 
    
          this.carte.splice(i, 1); 
        }
    
    }
    for( let i = 0; i < this.carte.length; i++){ 
  
      if ( this.carte[i] === this.essai1 ) { 
  
        this.carte.splice(i, 1); 
      }

  
  }


   this.$store.commit('setEssai1', "");

},
    
    clearCarte: function(num){

     
      var cart = document.getElementById(num);
     
      var essai= document.getElementById(this.essai1);

     

      cart.src="img/card-"+this.carteActive+".png";
      essai.src="img/card-"+this.carteActive+".png";
      

      this.$store.commit('setEssai1', "");
      
     
      
    },
     clearAllInterval: function(){

     
    var interval_id = window.setInterval("", 9999); // Get a reference to the last
                                                
for (var i = 0; i < interval_id; i++) {
        clearInterval(i);}
      
    },

         noneCarte: function( id){
      if(!id){
      for(var i=0;i<this.carte.length;i++){
        let im=this.carte[i];
        let img=document.getElementById(im);
         
          img.style="pointer-events:none";
       
      } }
      else {
        let img=document.getElementById(id);
         
        img.style="pointer-events:none";}
    },
          
       getTimer: function(){
             
        var seconde=null;
        var limit=30;
        var n =this.niveau+"";
          n=n.substring(n.length-1);
          n=parseInt(n, 10);
          switch (n) {
            case 9:
           limit=20;
          break;
            case 0:
            limit=15;
          break;
          default:
           limit=30;
          }
        var timestamp =Date.now();
        timestamp=Math.floor(timestamp/1000);
        var tim = this.time;
        
        if(tim!=0){
        var diff=timestamp-tim;
        var decompte=limit-diff;
       
        seconde=decompte;
        
        

        var rt=seconde/(limit/100);
        
        this.$store.commit('setTemps', rt);

        if(diff>=limit){
          
        this.$store.commit('setStopTimer', 1);
          
       this.minuteur(true);
       

             this.clearAllInterval();
         

             this.$store.commit('resetGame', false);
         
         this.$router.push('/');
       
        }
        }  
     
       },   
      minuteur: function(e){
     
       if(!e){

         
        this.t= setInterval(

          function () { 
          
           this.getTimer();
         
        }.bind(this) 
      , 1000);
       }
     
        if(this.stopTimer==1){
              clearInterval(this.t); this.$store.commit('setStopTimer', 0);
             this.clearAllInterval();
            }
         
    }
    ,
       
       },
          
        beforeMount(){
        if (this.id==0) {
          this.$router.push('/login');  
          }else{
            this.clearAllInterval();
            this.$store.commit('resetGame', false);
          this.createCarte(this.nbrCarte);  
           
       }
         
        },
}


</script>

