    width: 35%;
}


.bar-vie-off div{
    height:20px;
    background-color:#ececec;
}
.coeur-vie{

    width: 20px;
    height: 20px;
     margin-top: -12px;   
     margin-right: 1%; 
}
.bar-time-block{

    display: inline;


}
.bar-time{
    background-color:#94c6db;
    display: inline-block;
    text-align: center;

    width: 35%;
}

.bar-time div{
    height:20px;
